.slide-cart-container {
  min-height: 100%;
  min-width: 22%;
  background: white;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.slide-cart-container p {
  margin-top: 10px;
}

.slide-cart-wrapper {
  margin: 3em;
  display: flex;
  flex-direction: column;
  padding-top: 3em;
}

.cart {
  text-align: center;
}

.slide-cart-wrapper input {
  background-color: red;
  border: none;
  margin-top: 10px;
  padding: 1em;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.slide-cart-wrapper input:hover {
  background-color: darkred;
}

.cart-elems {
  padding-top: 2em;
  border-bottom: 0.5px solid lightgrey;
  padding-bottom: 0.1em;
}

.dark-body {
  min-height: 100%;
  min-width: 78%;
  background: black;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s ease-in;
}

.dark-body {
  animation: fadeIn ease 0.3s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.8;
  }
}

.cart-button-close {
  cursor: pointer;
  font-size: 1.5em;
  transition: 0.3s;
  top: 1vw;
  right: 1vw;
}

.cart-button-close:hover {
  transform: scale(1.3);
}

.slide-cart-wrapper li {
  margin: 5px;
}

.slide-cart-wrapper ul {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
  font-weight: bold;
}

.slide-cart-wrapper h2 {
  border-bottom: 0.5px solid lightgrey;
  margin-bottom: 10px;
}

.slide-cart-wrapper nav {
  display: none;
}

@media screen and (max-width: 500px) {
  .slide-cart-wrapper nav {
    display: block;
  }

  .slide-cart-container {
    min-width: 72%;
  }
}
