.upside {
  display: flex;
  justify-content: flex-start;
  transition: 0.4s;
  padding-top: 3vw;
}

.title {
  transform: rotate(-90deg);
  display: flex;
  height: fit-content;
  padding-top: 6.4vw;
  padding-right: 1em;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.title:hover {
  color: black;
}

.product-card-container {
  display: flex;
  flex-direction: column;
  padding-top: 0.2vw;
}

.product-card-item {
  padding-bottom: 1em;
  color: white;
}

.product-card-title {
  color: white;
  font-size: 28px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.product-card-title:hover {
  color: black;
}

.product-card-description {
  color: white;
  font-size: 16px;
  width: 39vh;
  height: 9vw;
  opacity: 0.8;
  letter-spacing: 0.8px;
  line-height: 22px;
}

.product-card-price {
  color: white;
  font-size: 20px;
}

.main-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
  padding: 0;
  margin: 0;
}

.next-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -1000px);
}

.previous-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 1000px);
}

@keyframes up {
  0% {
    transform: translate(-50%, 0px);
  }
  100% {
    transform: translate(-50%, -1000px);
  }
}

@keyframes down {
  0% {
    transform: translate(-50%, 0px);
  }
  100% {
    transform: translate(-50%, 1000px);
  }
}

@keyframes upnext {
  0% {
    transform: translate(-50%, -1000px);
  }
  100% {
    transform: translate(-50%, 0px);
  }
}

@keyframes downnext {
  0% {
    transform: translate(-50%, 1000px);
  }
  100% {
    transform: translate(-50%, 0px);
  }
}

.upside,
.downside {
  height: 50vh;
  width: 100%;
}

.downside {
  width: 100%;
  margin: 0;
  text-align: center;
  height: 50vh;
  padding-top: 120px;
  box-sizing: border-box;
  animation: slide 50s linear infinite;
}

.burger-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  color: white;
  display: none;
}

@media screen and (max-width: 500px) {
  .main-image {
    width: 17em;
    top: 65%;
  }

  .next-prev-container {
    margin-right: -2.5em !important;
    margin-top: -1em !important;
  }

  .title {
    margin-left: -2.6em;
    margin-top: 2.2em;
    font-size: 1.8em;
    padding-top: 9vw;
  }
  .product-card-title {
    width: 6em;
  }

  .product-card-container {
    height: 30em;
    margin-top: 1.2em;
    max-width: 20em;
    margin-left: 1.5vw;
  }

  .product-card-description {
    display: none;
  }
  .upside {
    height: 115vw !important;
  }

  .previous-image {
    width: 17em;
  }

  .next-image {
    width: 17em;
  }
}

.slide-cart-wrapper {
  margin-top: 1em;
  letter-spacing: 2px;
}

.slide-cart-wrapper h2 {
  text-align: center;
  padding-bottom: 0.5em;
}

.slide-cart-wrapper input {
  display: block;
  background: #79cd4c;
  text-transform: uppercase;
  color: white;
  margin-top: 2em;
}

.cart-title {
  margin-top: 2em;
  padding-bottom: 1em;
}

.cart {
  padding-top: 2em;
}

.cart-elems {
  padding-top: 2em;
}

@media screen and (max-width: 500px) {
  .burger-btn {
    display: block;
    top: 2.2em;
    right: 2.2em;
  }
}

@media screen and (max-width: 1700px) {
  .product-card-description {
    height: 16vw;
  }
}

@media screen and (max-width: 1100px) {
  .product-card-description {
    height: 15vw;
  }
}

@media screen and (max-width: 800px) {
  .product-card-description {
    height: 15vw;
  }
}

@media screen and (max-width: 900px) {
  .product-card-description {
    height: 18vw;
  }
}

@media screen and (max-width: 650px) {
  .product-card-description {
    height: 21vw;
  }
}

